import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label
 
} from 'reactstrap'
import Flatpickr from 'react-flatpickr'
import BreadCrumbs from '@components/breadcrumbs'
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import React, { Fragment, useState } from 'react'
import {  addfootballseries } from '../../store/actions'
import { useDispatch } from 'react-redux'
import { useForm, Controller } from 'react-hook-form'
import '@styles/react/libs/flatpickr/flatpickr.scss'

const selectstatus = [
  { value: '1', label: 'Live' },
  { value: '2', label: 'Upcoming' },
  { value: '3', label: 'Completed' },
  { value: '4', label: 'Cancelled' }
]
const selecttype = [
  { value: 'ODI', label: 'ODI' },
  { value: 'T20I', label: 'T20I' },
  { value: 'WOMANODI', label: 'WOMANODI' },
  { value: 'T20', label: 'T20' }
]
const VerticalForm = () => {
  const defaultValues = {
    reactSelect: null
  }

  const { register, errors, handleSubmit, control } = useForm({defaultValues})
  const dispatch = useDispatch()
  const [series, setSeries] = useState({ series_name:"", series_api_id:"", series_sh:"", date_time:"", series_status:"", series_logo:"", teams:"", type:'', active:''})
  const onSubmit = (e) => {
    e.preventDefault()
    console.log(series)
    dispatch(addfootballseries(series))
  }
  const onclear = (e) => {
    e.preventDefault()
    setSeries({  series_name:"", series_api_id:"", series_sh:"", date_time:"", series_status:"", series_logo:"", teams:"", type:'', active:'' })
  }

const editcategory = (e) => {
    
    setSeries({...series, series_status:e.value})
  }
  const typecategory = (e) => {
    
    setSeries({...series, type:e.value})
  }
  
  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Football Series' breadCrumbParent='Series' breadCrumbActive='football series add' />
    <Card>
      <CardHeader>
        <CardTitle tag="h4">Add Football Series</CardTitle>
      </CardHeader>
      <CardBody>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Row>
          <Col sm="12">
              <FormGroup>
                <Label for="seriesName">series Name</Label>
                <Input
                  type="name"
                  name="seriesName"
                  id="seriesName"
                  innerRef={register({ required: true })}
                  invalid={errors.seriesName && true}
                  value={series.series_name}
                  onChange={(e) => setSeries({ ...series, series_name: e.target.value })}
                  placeholder="Enter No of ID "
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for='series_api_id'>series_api_id</Label>
                <Input
                  type="number"
                  name="series_api_id"
                  id="series_api_id"
                  innerRef={register({ required: true })}
                  invalid={errors.series_api_id && true}
                  value={series.series_api_id}
                  onChange={(e) => setSeries({ ...series, series_api_id: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="series_sh">series_sh</Label>
                <Input
                  type="text"
                  name="series_sh"
                  id="series_sh"
                  innerRef={register({ required: true })}
                  invalid={errors.series_sh && true}
                  value={series.series_sh}
                  onChange={(e) => setSeries({ ...series, series_sh: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>               
                <Label for="date-time-picker">date Time</Label>
                <Flatpickr
                  data-enable-time
                  id="date-time-picker"
                  className="form-control"
                  // onChange={(date) => edittime(date)}
                  // key={date}
                  value={series?.date_time}
                  options={{
                    enableTime: true,
                    dateFormat: "Y-M-d H:i:s",
                    // defaultDate: new Date().toString()
                    defaultDate: "2020-11-26 14:30" 
                  }}
                  onChange={(selectedDates, dateStr) => {
                    const firstDate = selectedDates[0]
                    setSeries({ ...series, date_time:dateStr})
                    console.log({firstDate, dateStr })
                  }}
                />
                </FormGroup>
                </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="nameVertical">series_status</Label>
                <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                {...field}
                defaultValue={selectstatus[0]}                 
                  onChange={(e) => editcategory(e)}
                  options={selectstatus}
                  theme={selectThemeColors}                 
                  classNamePrefix='select'
                  isClearable={false}
                  />
                  )}
                  name='reactSelect'
            />
                {/* <Select
                  theme={selectThemeColors}
                  className="react-select"
                  classNamePrefix="select"
                  onChange={(e) => editcategory(e)}
                  options={selectstatus}
                  isClearable={false}
                /> */}
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="EmailVertical">Series Logo</Label>
                <Input
                  type="number"
                  name="Email"
                  id="EmailVertical"
                  innerRef={register({ required: true })}
                  invalid={errors.seriesName && true}
                  value={series.series_logo}
                  onChange={(e) => setSeries({ ...series, series_logo: e.target.value })}
                  placeholder="Enter No of Coins "
                />
              </FormGroup>
            </Col>
            <Col>
              <FormGroup>
                <Label for="nameVertical">Type</Label>
                <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                {...field}
                defaultValue={selecttype[0]}                 
                  onChange={(e) => typecategory(e)}
                  options={selecttype}
                  theme={selectThemeColors}                  
                  classNamePrefix='select'
                  isClearable={false}
                  />
                  )}
                  name='reactSelect'
            />
                {/* <Select
                  theme={selectThemeColors}
                  className="react-select"
                  classNamePrefix="select"
                  onChange={(e) => typecategory(e)}
                  options={selecttype}
                  isClearable={false}
                /> */}
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="active">Active</Label>
                <Input
                  type="number"
                  name="active"
                  id="active"
                  innerRef={register({ required: true })}
                  invalid={errors.active && true}
                  value={series.active}
                  onChange={(e) => setSeries({ ...series, active: e.target.value })}
                  placeholder="Enter No of ID "
                />
              </FormGroup>
            </Col>
          
          
            <Col sm="12">
              <FormGroup className="d-flex mb-0">
                <Button.Ripple
                  className="mr-1"
                  color="primary"
                  type="submit"
                >
                  Submit
                </Button.Ripple>
                <Button.Ripple
                  outline
                  color="secondary"
                  type="reset"
                  onClick={(e) => onclear(e)}
                >
                  Reset
                </Button.Ripple>
              </FormGroup>
            </Col>
          </Row>
        </Form>
      </CardBody>
    </Card>
    </Fragment>
  )
}
export default VerticalForm
